import React, { useEffect, useState } from "react";
import styled, { ThemeProvider } from "styled-components";
import { useDropzone, open } from "react-dropzone";
import {
  Navigate,
  useNavigate,
  Link,
  Toggle,
  redirect,
} from "react-router-dom";
import { backgrounds, lighten } from "polished";
import axios from "axios";
import theme from "../styles/theme"; //   
import NOTI from "../asset/KakaoTalk_20240105_025742662.png";
import USER from "../asset/userimage.png";
import COM from "../asset/cam.png";

//  
const ImageUpload = ({ onFileChange }) => {
  const [uploadedImage, setUploadedImage] = useState(USER);
  const { getRootProps, getInputProps } = useDropzone({
    accept: "image/*",
    onDrop: acceptedFiles => {
      const file = acceptedFiles[0];
      const reader = new FileReader();
      reader.onloadend = () => {
        //     
        setUploadedImage(reader.result);
        // Base64      
        onFileChange(reader.result);
      };
      //   Base64 
      reader.readAsDataURL(file);
    },
  });

  return (
    <div {...getRootProps()}>
      <input {...getInputProps()} />
      <img
        src={uploadedImage}
        alt="Uploaded"
        style={{
          border: "3px",
          borderColor: "red",
          marginTop: "15px",
          height: "80px",
          width: "80px",
          borderRadius: "50%",
          marginRight: "10px",
        }}
      />
    </div>
  );
};

const MainDiv = styled.div`
  // 
  display: flex;
  flex-basis: auto;
  flex-direction: row; //  
  align-items: center; //  
  justify-content: center; //  
  width: 100%;
  background-color: #ffffff;
  height: 100vh;
`;

const LogoDiv = styled.div`
  //   
  height: 100%;
  width: 50%; //  50%
  display: flex; //  ?
  flex-direction: column; // column :  (row : )
  align-items: center;
  justify-content: center;
`;

const MainLogo = styled.img`
  //
  width: 300px;
  height: 400px;
`;

const RegDiv = styled.div`
  //  
  height: auto;
  width: 50%; //  50%
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column; //  
`;

const RegBox = styled.div`
  //   
  border: 3px solid #b7babf;
  width: 350px;
  height: auto;
  padding: 30px;
  background-color: #f9f9f9;
  border-radius: 20px; // 
  display: flex; //    
  align-items: center; //   
  flex-direction: column; //  
`;

const MainTextBox = styled.div`
  //   
  // border: 1px solid blue;
  letter-spacing: 1px;
  color: #000000;
  text-align: center;
  font-size: 20px;
  width: 100%;
  height: 30px;
  font-weight: normal;
`;

const HorizontalBox = styled.div`
  //   
  display: flex; //    
  //flex-direction: row; // 
  justify-content: center; //  
  width: 100%;
  height: auto;
`;

const VerticalBox = styled.div`
  //   
  display: flex; //    
  align-items: center; //   
  flex-direction: column; // 
  width: 100%;
  height: auto;
`;

const SubTextBox = styled.div`
  //  textBox
  letter-spacing: 1px;
  color: #000000;
  width: 100%;
  height: 30px;
`;

const InputBox = styled.input`
  //  input box
  border: none; //  
  border-radius: 30px; //  
  background-color: #e3e4e6;
  color: #505050;
  font-size: 10px;
  text-align: center;
  width: 240px;
  height: 30px;
  margin-bottom: 2%;
`;

const InputBox2 = styled.input`
  //   input box
  border: none;
  width: 180px;
  height: 30px;
  border-radius: 30px; //  
  background-color: #f2f3f5;
  color: #000000;
  font-size: 10px;
  text-align: center;
`;

const Button2 = styled.button`
  // 
  border: none;
  height: 30px;
  width: 60px;
  top: 0;
  bottom: 0;
  margin: auto 0;
  border-radius: 3px;
  font-size: 10px;
  background-color: ${props => props.theme.color1};
  cursor: pointer;
  transition: background-color 0.3s ease;
  &:hover {
    background-color: ${props => lighten(0.1, props.theme.color1)};
  }
  color: #ffffff;
`;

const RegBtn = styled.button`
  //  
  border: none;
  width: 260px;
  height: 30px;
  border-radius: 40px; //  
  background-color: ${props => props.theme.color1};
  cursor: pointer;
  transition: background-color 0.3s ease;
  &:hover {
    background-color: ${props => lighten(0.1, props.theme.color1)};
  }
  color: #ffffff;
  font-size: 12px; // 
  font-weight: bold;
  letter-spacing: 1px; //  
  // transition: transform 80ms ease-in; //  
  text-align: center; //   
`;

const ThemedButton = styled.button`
  border: 4px solid #ffffff;
  height: 30px;
  width: 30px;
  padding: 10px;
  margin: 10px;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  transition: background-color 0.3s ease;

  /*     */
  background-color: ${props => props.theme.color1};
  color: #fff; //    

  &:hover {
    background-color: ${props => lighten(0.1, props.theme.color1)};
  }
`;

function Register() {
  const navigate = useNavigate();
  const [userNickname, setUserNickname] = useState(""); //   
  const [currentTheme, setCurrentTheme] = useState(theme.OrangeTheme); //    
  const [diaryTime, setDiaryTime] = useState(""); //    
  const [muteStartTime, setMuteStartTime] = useState(""); //     
  const [muteEndTime, setMuteEndTime] = useState(""); //     
  const token = window.localStorage.getItem("token");
  const [themeName, setThemeName] = useState("OrangeTheme"); //   
  const [selectedFile, setSelectedFile] = useState(null);

  //   
  const handleThemeChange = selectedThemeName => {
    const newTheme = theme[selectedThemeName];
    if (newTheme) {
      setCurrentTheme(newTheme); // UI   
      setThemeName(selectedThemeName); //     
    } else {
      console.error("Selected theme does not exist:", selectedThemeName);
    }
  };

  // jwt  userid  
  const getUserIdFromToken = () => {
    const payload = token.split(".")[1];
    const base642 = payload.replace(/-/g, "+").replace(/_/g, "/");
    const decodedPayload = atob(base642);
    const decodedJSON = JSON.parse(decodedPayload);

    console.log(decodedJSON);
    return decodedJSON.id.toString();
  };
  // Base64    
  const [base64Image, setBase64Image] = useState("");

  // ImageUpload  Base64    
  const handleFileChange = base64 => {
    setBase64Image(base64);
  };

  //    
  async function postUser() {
    const userId = getUserIdFromToken();
    try {
      const response = await axios.put(
        `/api/v1/user/${userId}`,
        {
          userNickname,
          userColor: themeName, //   
          diaryTime, //   
          muteStartTime, //    
          muteEndTime, //    
          userProfile: base64Image, // Base64   
        },
        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        },
      );
      if (response.status === 200 || response.status === 201) {
        //     
        //       
        localStorage.setItem("userTheme", themeName);
        //     , Welcome  
        navigate("/Welcome");
      }
    } catch (error) {
      console.error("Error posting user data:", error);
      //  
    }
  }
  //    
  const handleSubmit = async e => {
    e.preventDefault(); //      
    await postUser(); //   
    localStorage.setItem("userColor", currentTheme.color1); //    
  };

  return (
    <ThemeProvider theme={currentTheme}>
      <div>
        <MainDiv>
          <LogoDiv>
            <MainLogo src={NOTI} alt="" />
            <MainTextBox style={{ fontSize: "30px" }}>
                 
            </MainTextBox>
          </LogoDiv>
          <RegDiv>
            <RegBox>
              <MainTextBox style={{ marginTop: "10px" }}>
                 !
              </MainTextBox>
              <MainTextBox style={{ marginBottom: "20px" }}>
                 
              </MainTextBox>
              <HorizontalBox>
                <ImageUpload onFileChange={handleFileChange} />
                <VerticalBox>
                  <SubTextBox style={{ marginTop: "15px" }}>
                    *
                  </SubTextBox>
                  <HorizontalBox>
                    <InputBox2
                      id="user_name"
                      type="text"
                      placeholder=" (6~20)"
                      value={userNickname}
                      onChange={e => setUserNickname(e.target.value)}
                    />
                    <Button2 style={{ marginLeft: "5px" }}> </Button2>
                  </HorizontalBox>
                </VerticalBox>
              </HorizontalBox>
              <VerticalBox>
                <SubTextBox></SubTextBox>
                <InputBox
                  id="user_email"
                  type="text"
                  readOnly
                  style={{ width: "320px" }}
                />
                <HorizontalBox>
                  <SubTextBox>  </SubTextBox>
                </HorizontalBox>
                <InputBox2
                  id="mute_start"
                  type="time"
                  min="yyy"
                  max="zzz"
                  value={muteStartTime}
                  onChange={e => setMuteStartTime(e.target.value)}
                  style={{ width: "320px", marginBottom: "5px" }}
                />
                <InputBox2
                  id="mute_finish"
                  type="time"
                  min="yyy"
                  max="zzz"
                  value={muteEndTime}
                  onChange={e => setMuteEndTime(e.target.value)}
                  style={{ width: "320px" }}
                />
                <SubTextBox>   *</SubTextBox>
                <InputBox2
                  id="diary_create"
                  type="time"
                  min="yyy"
                  max="zzz"
                  value={diaryTime}
                  onChange={e => setDiaryTime(e.target.value)}
                  style={{ width: "320px", marginBottom: "5px" }}
                />
              </VerticalBox>
              <SubTextBox> </SubTextBox>
              <HorizontalBox>
                <ThemedButton
                  style={{ backgroundColor: theme.OrangeTheme.color1 }}
                  onClick={() => handleThemeChange("OrangeTheme")}
                ></ThemedButton>
                <ThemedButton
                  style={{ backgroundColor: theme.RedTheme.color1 }}
                  onClick={() => handleThemeChange("RedTheme")}
                ></ThemedButton>
                <ThemedButton
                  style={{ backgroundColor: theme.PinkTheme.color1 }}
                  onClick={() => handleThemeChange("PinkTheme")}
                ></ThemedButton>
                <ThemedButton
                  style={{ backgroundColor: theme.GreenTheme.color1 }}
                  onClick={() => handleThemeChange("GreenTheme")}
                ></ThemedButton>
                <ThemedButton
                  style={{ backgroundColor: theme.BlueTheme.color1 }}
                  onClick={() => handleThemeChange("BlueTheme")}
                ></ThemedButton>
              </HorizontalBox>

              <RegBtn
                style={{ marginTop: "30px" }}
                onClick={handleSubmit} //   postUser  
              >
                
              </RegBtn>
            </RegBox>
          </RegDiv>
        </MainDiv>
      </div>
    </ThemeProvider>
  );
}
export default Register;
