import React, { useState, useEffect } from "react";
import axios from "axios";
import styled, { ThemeProvider } from "styled-components";
import theme from "../styles/theme"; //   
import AddEventButton from "../components/AddEventButton";
import editIcon from "../asset/fi-rr-pencil.png"; // 
import deleteIcon from "../asset/fi-rr-trash.png"; // 
import DeleteModal from "../components/DeleteModal";
import ScheduleTimeTable from "../components/ScheduleTimeTable"; //  

const HorizontalBox = styled.div`
  min-width: 600px;
  //   
  display: flex; //    
  flex-direction: row; // 
  /* justify-content: center; //   */
  width: 100%;
`;

const ModalBackdrop = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
`;

const ModalContainer = styled.div`
  background-color: white;
  padding: 20px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  max-width: 400px;
  width: 100%;

  //   
  @media (max-width: 1050px) {
    // LeftSidebar   
    margin-right: 300px; // LeftSidebar     
  }
`;

const CloseButton = styled.button`
  align-self: flex-end;
`;

const ScheduleItem = styled.div`
  /*    */
  padding: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center; /*     */
  margin: 5px 0;
  cursor: pointer;
  &:hover {
    background-color: #f0f0f0;
  }
`;

const EventItem = styled.div`
  white-space: nowrap;
  background: ${props => props.theme.color2 || theme.OrangeTheme.color2};
  color: white;
  padding: 10px;
  border-radius: 20px;
  margin: 10px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  opacity: ${props => (props.completed ? "0.5" : "1")};
`;

const EventTitle = styled.div`
  white-space: nowrap;
  flex: 1; //     
  padding-right: 10px; //    
`;

const EditButton = styled.img`
  cursor: pointer;
  margin-left: 5px; //    
  width: 15px;
  height: 15px;
`;

const CompleteButton = styled.div`
  min-width: 30px;
  margin-right: 10px;
  height: 30px;
  width: 30px;
  border-radius: 50%;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
`;

const CheckMark = styled.div`
  color: black;
`;

const InputField = styled.input`
  width: 94%;
  padding: 10px;
  margin: 10px 0;
  border-radius: 10px;
  border: none;
  background-color: #f2f3f5;
`;

const ColorSelector = styled.div`
  display: flex;
  justify-content: space-around;
  margin: 10px 0;
`;

const ColorOption = styled.div`
  height: 30px;
  width: 30px;
  border-radius: 50%;
  background-color: ${props => props.color};
  cursor: pointer;
`;

const SubmitButton = styled.button`
  background-color: ${props => props.color || theme.OrangeTheme};
  color: white;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  cursor: pointer;
`;

const SubTextBox = styled.div`
  //  textBox
  letter-spacing: 1px;
  color: #000000;
  width: 100%;
  height: 0 auto;
  text-align: left;
  margin-left: 3px;
  font-weight: bold;
`;

const DateHeader = styled.div`
  font-size: 24px;
  text-align: center;
  margin-bottom: 20px;
  height: 40px;
  width: 100%;
  color: black;
  border-bottom: 2px solid
    ${props => props.theme.color1 || theme.OrangeTheme.color1};
`;

const DeleteButton = styled.img`
  min-width: 15px;
  // 
  cursor: pointer;
  margin-left: 10px; //     
  width: 15px;
  height: 15px;
`;

const EventList = styled.div`
  margin-top: 20px;
  width: 100%;
`;

const Event = styled.div`
  white-space: nowrap; //       
`;

const DateSpan = styled.span`
  margin-right: 15px; //    
`;

const RegDiv = styled.div`
  margin-left: 15px;
  //  
  height: auto;
  width: 85%; //  50%
  display: flex;
  /* justify-content: center; */
  align-items: center;
  flex-direction: column; //  
`;

const VerticalBox = styled.div`
  //   
  display: flex; //    
  align-items: center; //   
  flex-direction: column; // 
  width: 100%;
  height: auto;
`;
const TextBox = styled.div`
  font-size: 17px;
`;

const AddSchedulesButton = styled.button`
  margin-right: 15px;
  margin-top: 10px;
  padding: 5px 10px;
  background-color: ${props => props.theme.color1 || theme.OrangeTheme.color1};
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
`;

const ButtonContainer = styled.div`
  margin-top: -15px;
  margin-bottom: 10px;
  display: flex;
  justify-content: flex-end;
  width: 100%; //     .
`;

//    
const DeleteScheduleButton = styled.button`
  font-weight: bolder;
  width: 20px;
  padding: 0px;
  font-size: 100%;
  color: red;
  border: none;
  background-color: white;
`;

const ModalTitle = styled.div`
  font-size: 1.5em;
  margin-bottom: 0.5em;
`;

const ModalContent = styled.div`
  margin-bottom: 1.5em;
  font-size: 1em;
`;

//  
const CopyButton = styled.button`
  background-color: ${props => props.theme.color2 || theme.OrangeTheme.color1};
  color: white;
  padding: 10px 15px;
  border: none;
  border-radius: 5px;
  font-size: 1em;
  cursor: pointer;
`;

function CoopTodo({ teamId, onTodoChange, selectedDate }) {
  const [currentTheme, setCurrentTheme] = useState(theme.OrangeTheme); //   
  const token = window.localStorage.getItem("token");
  const [eventDate, setEventDate] = useState("");
  const [mySchedulesModalIsOpen, setMySchedulesModalIsOpen] = useState(false); //    
  const [myTeamModalIsOpen, setMyTeamModalIsOpen] = useState(false); //    
  const [mySchedules, setMySchedules] = useState([]); //   
  const [teamSchedules, setTeamSchedules] = useState([]);
  const [teamMembersCount, setTeamMembersCount] = useState(0);
  const [scheduleBlocks, setScheduleBlocks] = useState(Array(24 * 6).fill(0));

  const [todoStates, setTodoStates] = useState([]);

  //     
  const [events, setEvents] = useState([]);

  //        ID   
  const [isEditing, setIsEditing] = useState(false);
  const [editingTodoId, setEditingTodoId] = useState(null);

  //  ..   
  const formatDate = date => {
    const d = new Date(date);
    const year = d.getFullYear();
    const month = `0${d.getMonth() + 1}`.slice(-2); //  0  1 
    const day = `0${d.getDate()}`.slice(-2);
    return `${year}.${month}.${day}`;
  };
  //    
  const formatDateForInput = date => {
    const d = new Date(date);
    const year = d.getFullYear();
    const month = `0${d.getMonth() + 1}`.slice(-2); //  0  1 
    const day = `0${d.getDate()}`.slice(-2);
    return `${year}-${month}-${day}`; // YYYY-MM-DD  
  };
  //  
  const [isModalOpen, setIsModalOpen] = useState(false);

  //   , ,  
  const [title, setTitle] = useState([]);
  const [selectedColor, setSelectedColor] = useState(theme.OrangeTheme.color1);

  //     
  const handleDateChange = e => {
    setEventDate(e.target.value);
  };

  // isDeleteConfirmModalOpen    / 
  // deletingTodoId   ID 
  const [isDeleteConfirmModalOpen, setIsDeleteConfirmModalOpen] =
    useState(false);
  const [deletingTodoId, setDeletingTodoId] = useState(null);

  //   
  const getUserIdFromToken = () => {
    const payload = token.split(".")[1];
    const base642 = payload.replace(/-/g, "+").replace(/_/g, "/");
    const decodedPayload = atob(base642);
    const decodedJSON = JSON.parse(decodedPayload);

    console.log(decodedJSON);
    return decodedJSON.id.toString();
  };

  //      
  const fetchTeamData = async () => {
    try {
      const teamSchedulesResponse = await axios.get(
        `/api/v1/getSchedule/${teamId}`,
      );
      const userTeamResponse = await axios.get(`/api/v1/getUserTeam/${teamId}`);

      setTeamSchedules(teamSchedulesResponse.data);
      setTeamMembersCount(userTeamResponse.data.length);
    } catch (error) {
      console.error("Failed to fetch team data:", error);
    }
  };

  useEffect(() => {
    fetchTeamData();
  }, [teamId, onTodoChange]);

  //    
  const fetchUserData = async userToken => {
    const userId = getUserIdFromToken(userToken); //  ID 
    try {
      const response = await axios.get(`/api/v1/userInfo/${userId}`, {
        headers: {
          Authorization: `Bearer ${userToken}`,
        },
      });
      //       
      const userThemeName = response.data.userColor; //   

      //    
      if (theme[userThemeName]) {
        setCurrentTheme(theme[userThemeName]);
      }
    } catch (error) {
      console.error("Error fetching user data:", error);
    }
  };

  //       
  const handleColorSelect = colorKey => {
    setSelectedColor(colorKey); //    
  };
  // 
  const handleDeleteClick = teamTodoId => {
    setDeletingTodoId(teamTodoId);
    setIsDeleteConfirmModalOpen(true);
  };
  // 
  const closeDeleteConfirmModal = teamTodoId => {
    setIsDeleteConfirmModalOpen(false);
    setDeletingTodoId(teamTodoId);
  };
  //   
  const openModal = () => {
    setIsModalOpen(true);
  };

  //   
  const closeModal = () => {
    setIsModalOpen(false);
    setTitle("");
    setEventDate([]);
  };

  //        
  const openNewEventModal = () => {
    //   YYYY-MM-DD  
    const today = new Date();
    const formattedDate = today.toISOString().substring(0, 10);

    //    
    setTitle(""); //  
    setEventDate(formattedDate); //   
    setSelectedColor(currentTheme.color1); //   
    setIsEditing(false); //       
    setEditingTodoId(null); //    ID 
    openModal(); //   
  };

  //     
  const toggleComplete = async (teamTodoId, index) => {
    //     .
    const newCompletedStatus = !events[index].teamTodoDone;
    //       .
    try {
      const response = await axios.put(
        `/api/v1/updateTeamTodo/${teamId}/${teamTodoId}`,
        {
          ...events[index], //    
          teamTodoDone: newCompletedStatus, //   
        },
        {
          headers: {
            Authorization: `Bearer ${token}`, //   
          },
        },
      );

      if (response.status === 200) {
        //      events  
        const updatedEvents = events.map((event, evtIndex) =>
          evtIndex === index
            ? { ...event, teamTodoDone: newCompletedStatus }
            : event,
        );
        setEvents(updatedEvents);
      } else {
        //    ,  .
        console.error("Failed to update todo status:", response);
      }
    } catch (error) {
      //     ,  .
      console.error("Error updating todo status:", error);
    }
  };

  // 
  const handleDelete = async () => {
    if (!deletingTodoId) return;
    try {
      const response = await axios.delete(
        `/api/v1/deleteTeamTodo/${teamId}/${deletingTodoId}`,
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        },
      );
      if (response.status === 200) {
        const updatedEvents = events.filter(
          event => event.teamTodoId !== deletingTodoId,
        );
        setEvents(updatedEvents);
        closeDeleteConfirmModal(); //    
        setIsDeleteConfirmModalOpen(false);
        setDeletingTodoId(null); //    todo ID 
      } else {
        console.error("Failed to delete the event:", response);
      }
    } catch (error) {
      console.error("Error deleting the event:", error);
    }
  };

  //     
  const fetchTodoStates = async () => {
    const userId = getUserIdFromToken(); //  ID 
    try {
      const response = await axios.get(
        `/api/v1/getTodoState/${userId}/${teamId}`,
      );
      setTodoStates(response.data); // API       
    } catch (error) {
      console.error("Failed to fetch todo states:", error);
    }
  };

  //  Todo  
  const fetchTodos = async () => {
    try {
      const response = await axios.get(`/api/v1/getTeamTodo/${teamId}`, {
        headers: { Authorization: `Bearer ${token}` },
      });
      if (response.data && Array.isArray(response.data)) {
        //             
        const updatedEvents = response.data.map(event => ({
          ...event,
          //        ,      
          teamSelectedColor:
            currentTheme[event.teamTodoColor] || event.teamTodoColor,
        }));
        setEvents(updatedEvents);
      }
    } catch (error) {
      console.error("Failed to fetch todos:", error);
    }
  };
  //      
  const handleEditSubmit = async e => {
    setIsEditing(true);
    const eventData = {
      teamTodoTitle: title,
      teamTodoColor: selectedColor,
      teamTodoDone: false,
      teamTodoDate: formatDate(eventDate), //   
    };

    try {
      const url = `/api/v1/updateTeamTodo/${teamId}/${editingTodoId}`;
      const response = await axios.put(url, eventData, {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
      });

      if (response.status === 200 || response.status === 201) {
        await fetchTodos(); //   
        closeModal(); //   
      } else {
        console.error("Unexpected response:", response);
      }
    } catch (error) {
      console.error("Error updating event:", error);
    }
  };

  //      
  const handleAddSubmit = async e => {
    e.preventDefault(); //     
    const eventData = {
      teamTodoTitle: title,
      teamTodoColor: selectedColor,
      teamTodoDone: false,
      teamTodoDate: formatDate(eventDate),
    };

    try {
      const url = `/api/v1/createTeamTodo/${teamId}`;
      const response = await axios.post(url, eventData, {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
      });

      if (response.status === 200 || response.status === 201) {
        closeModal(); //   
        await fetchTodos(); //   
      } else {
        console.error("Unexpected response:", response);
      }
    } catch (error) {
      console.error("Error adding new event:", error);
    }
  };

  const handleEditClick = teamTodoId => {
    //     ID   .
    const eventToEdit = events.find(event => event.teamTodoId === teamTodoId);
    if (eventToEdit) {
      //      .
      // ,    .
      setTitle(eventToEdit.teamTodoTitle);
      setSelectedColor(eventToEdit.teamTodoColor); //  eventToEdit.SelectedColor ,     .
      //    ID  .
      setEditingTodoId(eventToEdit.teamTodoId);
      setEventDate(formatDateForInput(eventToEdit.teamTodoDate));
      //   .
      setIsEditing(true);

      //   .
      openModal();
    } else {
      console.error("Could not find the event to edit.");
    }
  };

  const handleTitleChange = e => {
    console.log("Before setTitle:", title);
    setTitle(e.target.value);
    console.log("After setTitle, title is now:", e.target.value);
  };

  // D-Day   
  const calculateDDay = date => {
    const today = new Date();
    const targetDate = new Date(date);
    const difference = targetDate - today;
    const dDay = Math.ceil(difference / (1000 * 60 * 60 * 24));

    // D-Day     
    if (dDay < 0) {
      return ""; // D-Day   ,  
    }
    if (dDay === 0) {
      return "D-Day"; // D-Day   "Day" 
    }
    return `D-${dDay}`; // D-Day   "D-" 
  };

  //    
  const fetchMySchedules = async () => {
    const userId = getUserIdFromToken(); //   ID 
    const formattedDate = formatDate(selectedDate); //   YYYY-MM-DD  

    try {
      //   ,  (`selectedDate`)  
      const response = await axios.get(
        `/api/v1/getTodo/${userId}?date=${formattedDate}`,
      );
      // `todoStartTime` ,     
      const filteredSchedules = response.data.filter(
        scheduleItem =>
          scheduleItem.todoStartTime && scheduleItem.todoDate === formattedDate,
      );
      setMySchedules(filteredSchedules); //     
    } catch (error) {
      console.error("Failed to fetch my schedules:", error);
    }
  };

  const fetchTeamId = async () => {};

  //      
  const openMySchedulesModal = () => {
    fetchMySchedules(); //      
    setMySchedulesModalIsOpen(true);
  };

  const openShareModal = () => {
    fetchTeamId();
    setMyTeamModalIsOpen(true);
  };

  //      
  const closeMySchedulesModal = () => {
    setMySchedulesModalIsOpen(false);
  };

  const closeMyTeamModal = () => {
    setMyTeamModalIsOpen(false);
  };

  //   TimeTable  
  const handleSelectSchedule = async scheduleId => {
    //   TimeTable  
    try {
      await axios.post(
        `/api/v1/inputSchedule/${teamId}/${scheduleId}`,
        {},
        {
          headers: { Authorization: `Bearer ${token}` },
        },
      );
      // closeMySchedulesModal(); //     
      onTodoChange(); //    
    } catch (error) {
      console.error("Failed to input schedule:", error);
    }
  };

  useEffect(() => {
    const timeToIndex = time => {
      const [hours, minutes] = time.split(":").map(Number);
      return hours * 6 + Math.floor(minutes / 10);
    };

    const updateScheduleBlocks = () => {
      const formattedSelectedDate = formatDateForInput(selectedDate);
      const schedulesForSelectedDate = teamSchedules.filter(
        scheduleItem =>
          formatDateForInput(scheduleItem.todoDate) === formattedSelectedDate,
      );

      const newScheduleBlocks = Array(24 * 6).fill(0);

      schedulesForSelectedDate.forEach(scheduleItem => {
        if (scheduleItem.todoStartTime && scheduleItem.todoEndTime) {
          const startIndex = timeToIndex(scheduleItem.todoStartTime);
          // :     endIndex  
          const endIndex = timeToIndex(scheduleItem.todoEndTime); //  
          for (let i = startIndex; i < endIndex; i += 1) {
            // '<='  '<' 
            newScheduleBlocks[i] += 1;
          }
        }
      });

      //       
      const membersCount = teamMembersCount || 1; //   0    1 
      setScheduleBlocks(newScheduleBlocks.map(block => block / membersCount));
    };

    updateScheduleBlocks();
  }, [teamSchedules, teamMembersCount, selectedDate]);

  //   TimeTable   
  const handleDeleteSchedule = async todoId => {
    try {
      await axios.delete(`/api/v1/deleteSchedule/${teamId}/${todoId}`, {
        headers: { Authorization: `Bearer ${token}` },
      });
      //   , TimeTable     
      fetchMySchedules(); //     
      onTodoChange(); // TimeTable        
    } catch (error) {
      console.error("Failed to delete the schedule:", error);
    }
  };

  useEffect(() => {
    fetchUserData();
    fetchTodos(); //  Todo    
    if (mySchedulesModalIsOpen) {
      fetchMySchedules();
      fetchTodoStates();
    }
  }, [onTodoChange, selectedDate, mySchedulesModalIsOpen]);

  const handleCopyClipBoard = text => {
    try {
      navigator.clipboard.writeText(text);
      alert(" .");
    } catch (error) {
      alert("  .");
    }
  };

  return (
    <ThemeProvider theme={currentTheme}>
      <HorizontalBox>
        <RegDiv>
          <EventList>
            {events.map((event, index) => (
              <EventItem
                key={event.teamTodoId}
                style={{
                  backgroundColor: event.teamSelectedColor, //     
                  opacity: event.teamTodoDone ? "0.5" : "1", //     
                }}
              >
                <CompleteButton
                  onClick={() => toggleComplete(event.teamTodoId, index)}
                >
                  {event.teamTodoDone && <CheckMark></CheckMark>}
                </CompleteButton>
                <EventTitle>{event.teamTodoTitle}</EventTitle>
                <Event>
                  <DateSpan>{formatDate(event.teamTodoDate)}</DateSpan>
                  <DateSpan>{calculateDDay(event.teamTodoDate)}</DateSpan>
                </Event>
                <EditButton
                  src={editIcon}
                  alt=""
                  onClick={() =>
                    !event.teamTodoDone && handleEditClick(event.teamTodoId)
                  }
                />
                <DeleteButton
                  src={deleteIcon}
                  alt=""
                  onClick={() =>
                    !event.teamTodoDone && handleDeleteClick(event.teamTodoId)
                  }
                />
                <DeleteModal
                  isOpen={isDeleteConfirmModalOpen}
                  onClose={closeDeleteConfirmModal}
                  onConfirm={handleDelete}
                />
              </EventItem>
            ))}
          </EventList>
          <AddEventButton onClick={openNewEventModal}>
            +   
          </AddEventButton>
          {isModalOpen && (
            <ModalBackdrop onClick={closeModal}>
              <ModalContainer onClick={e => e.stopPropagation()}>
                <CloseButton
                  onClick={closeModal}
                  style={{ border: "none", backgroundColor: "white" }}
                >
                  x
                </CloseButton>
                <DateHeader
                  style={{
                    borderBottom: `2px solid ${currentTheme[selectedColor]}`,
                    fontSize: "20px",
                    height: "10px",
                  }}
                ></DateHeader>
                <SubTextBox> </SubTextBox>
                <InputField
                  placeholder=" !"
                  value={title}
                  onChange={handleTitleChange}
                  maxLength={15}
                  style={{ marginBottom: "30px" }}
                />
                <SubTextBox> </SubTextBox>
                <InputField
                  type="date"
                  value={eventDate}
                  onChange={handleDateChange}
                  style={{ marginBottom: "30px" }}
                />
                <SubTextBox> </SubTextBox>
                <ColorSelector style={{ marginBottom: "30px" }}>
                  {Object.keys(currentTheme)
                    .filter(key => key.startsWith("color"))
                    .map((colorKey, index) => (
                      <ColorOption
                        key={index}
                        color={currentTheme[colorKey]}
                        onClick={() => handleColorSelect(colorKey)}
                      />
                    ))}
                </ColorSelector>
                <SubmitButton
                  color={currentTheme[selectedColor]}
                  onClick={isEditing ? handleEditSubmit : handleAddSubmit}
                >
                  {isEditing ? "" : " "}
                </SubmitButton>
              </ModalContainer>
            </ModalBackdrop>
          )}
        </RegDiv>
        <VerticalBox>
          <ButtonContainer>
            <AddSchedulesButton onClick={openMySchedulesModal}>
              + add Schedules
            </AddSchedulesButton>
            <AddSchedulesButton onClick={openShareModal}>
              
            </AddSchedulesButton>
          </ButtonContainer>
          <TextBox>{formatDate(selectedDate)}</TextBox>
          <div>
            {mySchedulesModalIsOpen && (
              <ModalBackdrop onClick={closeMySchedulesModal}>
                <ModalContainer onClick={e => e.stopPropagation()}>
                  <CloseButton
                    onClick={closeMySchedulesModal}
                    style={{ border: "none", backgroundColor: "white" }}
                  >
                    x
                  </CloseButton>
                  <h2>{formatDate(selectedDate)} </h2>
                  {mySchedules.map(scheduleItem => {
                    //      
                    const isAdded = todoStates.some(
                      todoState =>
                        todoState.todoId === scheduleItem.todoId &&
                        todoState.state,
                    );

                    //      
                    const itemStyle = isAdded
                      ? {
                          marginLeft: "10px",
                          opacity: "0.5",
                          textDecoration: "line-through",
                          pointerEvents: "none",
                          cursor: "default",
                        }
                      : {};

                    return (
                      <ScheduleItem
                        key={scheduleItem.todoId}
                        onClick={() =>
                          !isAdded
                            ? handleSelectSchedule(scheduleItem.todoId)
                            : null
                        }
                      >
                        {/*     */}
                        {isAdded && (
                          <DeleteScheduleButton
                            onClick={e => {
                              e.stopPropagation(); //     
                              handleDeleteSchedule(scheduleItem.todoId);
                            }}
                          >
                            
                          </DeleteScheduleButton>
                        )}
                        <div
                          style={{
                            display: "flex",
                            alignItems: "center",
                            flexGrow: 1,
                            ...itemStyle,
                            justifyContent: "space-between",
                            width: "100%",
                          }}
                        >
                          <div>{scheduleItem.todoTitle}</div> {/* Title */}
                          <div>
                            {scheduleItem.todoStartTime} {/* Start Time */}
                            {scheduleItem.todoEndTime &&
                              `~ ${scheduleItem.todoEndTime}`}{" "}
                            {/* End Time, if available */}
                          </div>
                        </div>
                      </ScheduleItem>
                    );
                  })}
                </ModalContainer>
              </ModalBackdrop>
            )}
            {/*  TimeTable     */}
          </div>
          <div>
            {myTeamModalIsOpen && (
              <ModalBackdrop onClick={closeMyTeamModal}>
                <ModalContainer onClick={e => e.stopPropagation()}>
                  <CloseButton
                    onClick={closeMyTeamModal}
                    style={{ border: "none", backgroundColor: "white" }}
                  >
                    x
                  </CloseButton>
                  <ModalTitle>   !</ModalTitle>
                  <ModalContent> : {teamId}</ModalContent>
                  <CopyButton onClick={() => handleCopyClipBoard(teamId)}>
                    
                  </CopyButton>
                </ModalContainer>
              </ModalBackdrop>
            )}
          </div>
          <ScheduleTimeTable
            style={{ width: "300px", height: "450px", marginBottom: "5px" }}
            scheduleBlocks={scheduleBlocks} // schedule  scheduleBlocks prop 
          />
        </VerticalBox>
      </HorizontalBox>
    </ThemeProvider>
  );
}

export default CoopTodo;
