import React, { useState, useEffect } from "react";
import styled, { ThemeProvider } from "styled-components";
import { useDropzone, open } from "react-dropzone";
import {
  Navigate,
  useNavigate,
  Link,
  Toggle,
  redirect,
} from "react-router-dom";
import { backgrounds, lighten } from "polished";
import axios from "axios"; // axios import 
import DeleteModal from "../components/DeleteModal";
import theme from "../styles/theme"; //   
import editIcon from "../asset/fi-rr-pencil.png"; // 
import deleteIcon from "../asset/fi-rr-trash.png"; // 
import TimeTable from "../components/TimeTable"; // 
import DiaryContainer from "../components/DiaryContainer";
import AddEventButton from "../components/AddEventButton";

const ModalBackdrop = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
`;

const ModalContainer = styled.div`
  background-color: white;
  padding: 20px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  max-width: 400px;
  width: 100%;

  //   
  @media (max-width: 1050px) {
    // LeftSidebar   
    margin-right: 300px; // LeftSidebar     
  }
`;

const CloseButton = styled.button`
  align-self: flex-end;
`;

const EventList = styled.div`
  width: 100%;
`;

const EventItem = styled.div`
  background: ${props => props.theme.color2 || theme.OrangeTheme.color2};
  color: white;
  padding: 10px;
  border-radius: 20px;
  margin: 10px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  opacity: ${props => (props.completed ? "0.5" : "1")};
`;

const EventTitle = styled.div`
  flex: 1; //     
  padding-right: 10px; //    
`;

const EditButton = styled.img`
  cursor: pointer;
  margin-left: 10px; //    
  width: 15px;
  height: 15px;
`;

const EventTime = styled.div`
  white-space: nowrap; //       
`;

const CompleteButton = styled.div`
  margin-right: 10px;
  height: 30px;
  width: 30px;
  border-radius: 50%;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
`;

const CheckMark = styled.div`
  color: black;
`;

const InputField = styled.input`
  width: 94%;
  padding: 10px;
  margin: 10px 0;
  border-radius: 10px;
  border: none;
  background-color: #f2f3f5;
`;

const TimeInput = styled(InputField).attrs({ type: "time" })``;

const ColorSelector = styled.div`
  display: flex;
  justify-content: space-around;
  margin: 10px 0;
`;

const ColorOption = styled.div`
  height: 30px;
  width: 30px;
  border-radius: 50%;
  background-color: ${props => props.color};
  cursor: pointer;
`;

const SubmitButton = styled.button`
  background-color: ${props => props.color || theme.OrangeTheme};
  color: white;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  cursor: pointer;
`;

const SubTextBox = styled.div`
  //  textBox
  letter-spacing: 1px;
  color: #000000;
  width: 100%;
  height: 0 auto;
  text-align: left;
  margin-left: 3px;
  font-weight: bold;
`;

const DateHeader = styled.div`
  font-size: 24px;
  text-align: center;
  margin-bottom: 20px;
  height: 40px;
  width: 100%;
  color: black;
  border-bottom: 2px solid
    ${props => props.theme.color1 || theme.OrangeTheme.color1};
`;

const Edit = styled.img`
  //
  width: 20px;
  height: 20px;
`;

const DeleteButton = styled.img`
  // 
  cursor: pointer;
  margin-left: 10px; //     
  width: 15px;
  height: 15px;
`;

function Todo({ selectedDate }) {
  //      
  const [events, setEvents] = useState([]);
  const navigate = useNavigate();
  const [currentTheme, setCurrentTheme] = useState(theme.OrangeTheme); //   

  //       
  const [schedule, setSchedule] = useState(Array(24 * 6).fill(false));

  //        ID   
  const [isEditing, setIsEditing] = useState(false);
  const [editingTodoId, setEditingTodoId] = useState(null);

  //  ..   
  const formatDate = date => {
    const d = new Date(date);
    const year = d.getFullYear();
    const month = `0${d.getMonth() + 1}`.slice(-2); //  0  1 
    const day = `0${d.getDate()}`.slice(-2);
    return `${year}.${month}.${day}`;
  };
  //  
  const [isModalOpen, setIsModalOpen] = useState(false);

  //   , ,  
  const [title, setTitle] = useState([]);
  const [startTime, setStartTime] = useState([]);
  const [endTime, setEndTime] = useState([]);
  const [selectedColor, setSelectedColor] = useState(theme.OrangeTheme.color1);

  const token = window.localStorage.getItem("token");

  // isDeleteConfirmModalOpen    / 
  // deletingTodoId   ID 
  const [isDeleteConfirmModalOpen, setIsDeleteConfirmModalOpen] =
    useState(false);
  const [deletingTodoId, setDeletingTodoId] = useState(null);

  //   
  const getUserIdFromToken = () => {
    const payload = token.split(".")[1];
    const base642 = payload.replace(/-/g, "+").replace(/_/g, "/");
    const decodedPayload = atob(base642);
    const decodedJSON = JSON.parse(decodedPayload);

    console.log(decodedJSON);
    return decodedJSON.id.toString();
  };

  //       
  const fetchUserData = async () => {
    const userId = getUserIdFromToken(); //  ID 
    const formattedDate = formatDate(selectedDate); //   YYYY-MM-DD  

    try {
      //   
      const userResponse = await axios.get(`/api/v1/userInfo/${userId}`, {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
      });

      //     
      if (userResponse.status === 200) {
        const userThemeName = userResponse.data.userColor; //   

        //    
        if (theme[userThemeName]) {
          setCurrentTheme(theme[userThemeName]);

          //     
          const eventsResponse = await axios.get(
            `/api/v1/getTodo/${userId}?date=${formattedDate}`,
            {
              headers: {
                Authorization: `Bearer ${localStorage.getItem("token")}`,
              },
            },
          );

          if (eventsResponse.status === 200) {
            const filteredEvents = eventsResponse.data.filter(
              event => event.todoDate === formattedDate,
            );
            const updatedEvents = filteredEvents.map(event => ({
              ...event,
              selectedColor:
                theme[userThemeName][event.todoColor] || event.todoColor,
            }));
            setEvents(updatedEvents);
          } else {
            console.error("Unexpected response:", eventsResponse);
          }
        }
      } else {
        console.error("Unexpected response:", userResponse);
      }
    } catch (error) {
      console.error("Error fetching user data and events:", error);
    }
  };

  //       
  const handleColorSelect = colorKey => {
    setSelectedColor(colorKey); //    
  };
  //   
  const handleDeleteClick = todoId => {
    setDeletingTodoId(todoId);
    setIsDeleteConfirmModalOpen(true);
  };
  //  
  const closeDeleteConfirmModal = () => {
    setIsDeleteConfirmModalOpen(false);
    setDeletingTodoId(null);
  };
  //   
  const openModal = () => {
    setIsModalOpen(true);
  };

  //   
  const closeModal = () => {
    setIsModalOpen(false);
    setTitle("");
    setStartTime("");
    setEndTime("");
  };

  //      
  const handleEditClick = todoId => {
    //    ID   .
    const eventToEdit = events.find(event => event.todoId === todoId);
    if (eventToEdit) {
      //       .
      setTitle(eventToEdit.todoTitle);
      setStartTime(eventToEdit.todoStartTime);
      setEndTime(eventToEdit.todoEndTime);
      setSelectedColor(eventToEdit.selectedColor); //      .
      setEditingTodoId(eventToEdit.todoId); //    ID  .
      setIsEditing(true); //   .
      openModal(); //   .
    }
  };

  //        
  const openNewEventModal = () => {
    //    
    setSelectedColor(currentTheme.color1); //   
    setIsEditing(false); //       
    setEditingTodoId(null); //    ID 
    openModal(); //   
  };

  //  schedule    
  const timeToIndex = time => {
    const [hours, minutes] = time.split(":").map(Number);
    return hours * 6 + Math.floor(minutes / 10);
  };

  //     
  const toggleComplete = async (todoId, index) => {
    const userId = getUserIdFromToken(); //  ID 
    //     .
    const newCompletedStatus = !events[index].todoDone;

    //       .
    try {
      const response = await axios.put(
        `/api/v1/updateTodo/${userId}/${todoId}`,
        {
          ...events[index], //    
          todoDone: newCompletedStatus, //   
        },
        {
          headers: {
            Authorization: `Bearer ${token}`, //   
          },
        },
      );

      if (response.status === 200) {
        //      events  
        const updatedEvents = events.map((event, evtIndex) =>
          evtIndex === index
            ? { ...event, todoDone: newCompletedStatus }
            : event,
        );
        setEvents(updatedEvents);
        /** toggleComplete  todoId index  ,
         *   todoDone   .
         *    ,
         * events  schedule    .
         * schedule        
         *  . */
        //    schedule   .
        const startTimeIndex = timeToIndex(updatedEvents[index].todoStartTime);
        const endTimeIndex = timeToIndex(updatedEvents[index].todoEndTime);
        const newSchedule = schedule.map((slot, idx) => {
          if (idx >= startTimeIndex && idx < endTimeIndex) {
            return newCompletedStatus
              ? updatedEvents[index].selectedColor
              : false;
          }
          return slot;
        });

        setSchedule(newSchedule);
      } else {
        //    ,  .
        console.error("Failed to update todo status:", response);
      }
    } catch (error) {
      //     ,  .
      console.error("Error updating todo status:", error);
    }
  };

  const handleDelete = async () => {
    if (!deletingTodoId) return;
    const userId = getUserIdFromToken();
    try {
      const response = await axios.delete(
        `/api/v1/deleteTodo/${userId}/${deletingTodoId}`,
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        },
      );
      if (response.status === 200) {
        const updatedEvents = events.filter(
          event => event.todoId !== deletingTodoId,
        );
        setEvents(updatedEvents);
        closeDeleteConfirmModal(); //    
        setIsDeleteConfirmModalOpen(false);
        setDeletingTodoId(null); //    todo ID 
      } else {
        console.error("Failed to delete the event:", response);
      }
    } catch (error) {
      console.error("Error deleting the event:", error);
    }
  };
  //  ''  ''     
  const handleSubmit = async () => {
    const userId = getUserIdFromToken(); //  ID 
    const eventData = {
      todoTitle: title,
      todoStartTime: startTime,
      todoEndTime: endTime,
      todoColor: selectedColor,
      todoDone: false, //    false 
      todoDate: formatDate(selectedDate), // formatDate     
    };

    try {
      let response;
      if (isEditing) {
        //  
        const url = `/api/v1/updateTodo/${userId}/${editingTodoId}`; //  API , todoId 
        response = await axios.put(url, eventData, {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        });
      } else {
        //   
        const url = `/api/v1/createTodo/${userId}`; //  API 
        response = await axios.post(url, eventData, {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        });
      }
      if (response.status === 200 || response.status === 201) {
        closeModal(); //   
        //     (:     )
        await fetchUserData(); //     .
      } else {
        //      
        console.error("Unexpected response:", response);
      }
    } catch (error) {
      console.error("Error updating/adding event:", error);
      //  
    }
  };

  // useEffect       
  useEffect(() => {
    fetchUserData();
  }, [selectedDate]); // token     

  // events    TimeTable   useEffect
  useEffect(() => {
    const newSchedule = Array(24 * 6).fill(null); //    .

    //      .
    events.forEach(event => {
      if (event.todoDone) {
        const startIdx = timeToIndex(event.todoStartTime);
        const endIdx = timeToIndex(event.todoEndTime);
        for (let i = startIdx; i < endIdx; i += 1) {
          newSchedule[i] = event.todoDone
            ? event.selectedColor
            : `${event.selectedColor}80`; // 80  
        }
      }
    });

    setSchedule(newSchedule); // schedule  .
  }, [events]); // events    .

  const handleTitleChange = e => {
    console.log("Before setTitle:", e.target.value);
    setTitle(e.target.value);
    console.log("After setTitle, title is now:", title);
  };

  return (
    <ThemeProvider theme={currentTheme}>
      <div>
        <DiaryContainer>
          <DateHeader>{formatDate(selectedDate)}</DateHeader>
          <EventList>
            {/*      .
            EventList   events       */}
            {events.map((event, index) => (
              <EventItem
                key={event.todoId}
                style={{
                  backgroundColor: event.selectedColor, //     
                  opacity: event.todoDone ? "0.5" : "1", //     
                }}
              >
                <CompleteButton
                  onClick={() => toggleComplete(event.todoId, index)}
                >
                  {event.todoDone && <CheckMark></CheckMark>}
                </CompleteButton>
                <EventTitle>{event.todoTitle}</EventTitle>
                <EventTime>
                  {event.todoStartTime}{" "}
                  {event.todoEndTime && `~ ${event.todoEndTime}`}
                </EventTime>
                <EditButton
                  src={editIcon}
                  alt=""
                  onClick={() =>
                    !event.todoDone && handleEditClick(event.todoId)
                  }
                />
                <DeleteButton
                  src={deleteIcon}
                  alt=""
                  onClick={() =>
                    !event.todoDone && handleDeleteClick(event.todoId)
                  }
                />
                <DeleteModal
                  isOpen={isDeleteConfirmModalOpen}
                  onClose={closeDeleteConfirmModal}
                  onConfirm={handleDelete}
                />
              </EventItem>
            ))}
          </EventList>
          <AddEventButton onClick={openNewEventModal}>
            +   
          </AddEventButton>
          {isModalOpen && (
            <ModalBackdrop onClick={closeModal}>
              <ModalContainer onClick={e => e.stopPropagation()}>
                <CloseButton
                  onClick={closeModal}
                  style={{ border: "none", backgroundColor: "white" }}
                >
                  x
                </CloseButton>
                <DateHeader
                  style={{
                    borderBottom: `2px solid ${currentTheme[selectedColor]}`,
                    fontSize: "20px",
                    height: "30px",
                  }}
                >
                  {formatDate(selectedDate)}
                </DateHeader>
                <SubTextBox> </SubTextBox>

                <InputField
                  placeholder=" !"
                  value={title}
                  onChange={handleTitleChange}
                  maxLength={15}
                  style={{ marginBottom: "30px" }}
                />
                <SubTextBox> </SubTextBox>
                <TimeInput
                  value={startTime}
                  onChange={e => setStartTime(e.target.value)}
                />
                <SubTextBox> </SubTextBox>
                <TimeInput
                  value={endTime}
                  onChange={e => setEndTime(e.target.value)}
                  style={{ marginBottom: "30px" }}
                />
                <SubTextBox> </SubTextBox>
                <ColorSelector style={{ marginBottom: "30px" }}>
                  {Object.keys(currentTheme)
                    .filter(key => key.startsWith("color"))
                    .map((colorKey, index) => (
                      <ColorOption
                        key={index}
                        color={currentTheme[colorKey]}
                        onClick={() => handleColorSelect(colorKey)}
                      />
                    ))}
                </ColorSelector>
                <SubmitButton
                  color={currentTheme[selectedColor]}
                  onClick={handleSubmit}
                >
                  {isEditing ? "" : " "}{" "}
                </SubmitButton>
              </ModalContainer>
            </ModalBackdrop>
          )}

          <TimeTable schedule={schedule} />
        </DiaryContainer>
      </div>
    </ThemeProvider>
  );
}
export default Todo;
