import React, { useState, useEffect, useRef } from "react";
import axios from "axios";
import styled, { ThemeProvider } from "styled-components";
import theme from "../styles/theme"; //   zz
import SEND from "../asset/fi-rr-paper-plane.png";

const ChatDiv = styled.div`
  border-radius: 10px;
  border: 2px solid ${props => props.theme.color1 || theme.OrangeTheme.color1}; //  
  /* border: 3px solid black; */
  margin-top: 10px;
  height: 500px;
  /* justify-content: flex-end; //   */
  /* justify-content: bottom; */
  align-items: bottom;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -ms-overflow-style: none;

  ::-webkit-scrollbar {
    display: none;
  }
`;

const ChatInputDiv = styled.div`
  height: 35px;
  /* border-radius: 20px; */
  width: 100%; // :        
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #e3e4e6;
  padding: 10px;
  box-sizing: border-box; //    
`;

const ChatInput = styled.input`
  border: none;
  background-color: #e3e4e6; //  
  flex-grow: 1;
  padding: 5px;
`;

const ChatRole = styled.div`
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.2);
  &.bot-message {
    background-color: #e8e8e8; //  
    align-self: flex-start; //  
    width: 200px;
    border-radius: 15px;
    padding: 10px;
    font-size: 12px;
  }
  &.user-message {
    background-color: white; //  
    border-radius: 15px;
    padding: 10px;
    font-size: 12px;
    align-self: flex-end; //  
    margin-bottom: 15px;
    margin-top: 15px;
  }
`;
const SendButton = styled.img`
  padding: 10;
  cursor: pointer;
  width: 20px; //   
`;

const MessagesContainer = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: flex-end; //  
  flex-grow: 1; //  flex-direction column flex-grow    .
  overflow-y: auto; //     .
  padding: 10px; //      .
  margin-bottom: 10px; //   .
`;

function ChatComponent() {
  const [messages, setMessages] = useState([]);
  const [newMessage, setNewMessage] = useState("");
  const token = window.localStorage.getItem("token");
  const messagesEndRef = useRef(null); //      ref

  const getUserIdFromToken = () => {
    const payload = token.split(".")[1];
    const base642 = payload.replace(/-/g, "+").replace(/_/g, "/");
    const decodedPayload = atob(base642);
    const decodedJSON = JSON.parse(decodedPayload);

    console.log(decodedJSON);
    return decodedJSON.id.toString();
  };
  const userId = getUserIdFromToken();

  const fetchChatList = async () => {
    try {
      const response = await axios.get(`/api/v3/chatlist/${userId}`);
      setMessages(response.data);
    } catch (error) {
      console.error("     .", error);
    }
  };
  //   
  useEffect(() => {
    fetchChatList();
    //       
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  }, [newMessage]); // messages    effect 

  //    
  const sendMessage = async event => {
    event.preventDefault();
    try {
      const response = await axios.post(`/api/v3/ask/${userId}`, {
        chat_content: newMessage,
        chatWho: false, // ,   
      });
      setMessages([
        ...messages,
        { chat_content: newMessage, chatWho: false },
        { chat_content: response.data, chatWho: true },
      ]);
      setNewMessage("");
    } catch (error) {
      console.error("    .", error);
    }
  };

  //    Enter   
  const handleKeyPress = event => {
    if (event.key === "Enter") {
      sendMessage(event);
    }
  };

  const scrollRef = useRef();
  console.log(scrollRef.current);

  return (
    <ChatDiv>
      <MessagesContainer>
        {messages.map((msg, index) => (
          <ChatRole
            key={index}
            className={msg.chatWho ? "bot-message" : "user-message"}
          >
            {msg.chatContent}
          </ChatRole>
        ))}
        <div ref={messagesEndRef} />
      </MessagesContainer>
      <ChatInputDiv>
        <ChatInput
          type="text"
          value={newMessage}
          onChange={e => setNewMessage(e.target.value)}
          onKeyPress={handleKeyPress}
          placeholder="   !"
        />
        <SendButton src={SEND} alt="" onClick={sendMessage} />
      </ChatInputDiv>
    </ChatDiv>
  );
}

export default ChatComponent;
