//  
import React, { useState, useEffect } from "react";
import styled, { ThemeProvider } from "styled-components";
import { useDropzone, open } from "react-dropzone";
import {
  Navigate,
  useNavigate,
  Link,
  Toggle,
  redirect,
  useParams,
} from "react-router-dom";
import { backgrounds, lighten } from "polished";
import { format } from "date-fns"; //    
import axios from "axios";
import theme from "../styles/theme"; //   
import CoopTodo from "../components/CoopTodo.jsx";
import Memo from "../components/Memo.jsx";
import NavBar from "../components/Navigation";

const MainDiv = styled.div`
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 350px;
  margin-left: 350px;
  @media (max-width: 1050px) {
    margin-left: 0;
    padding-left: 20px;
    padding-right: 20px;
  }
  padding-top: 170px;
`;

const DateHeader = styled.div`
  font-size: 24px;
  text-align: center;
  margin-bottom: 20px;
  height: 40px;
  width: 100%;
  color: black;
  border-bottom: 2px solid
    ${props => props.theme.color1 || theme.OrangeTheme.color1};
`;

function CoopDetail({ team }) {
  const [teamDetails, setTeamDetails] = useState(""); //    
  const token = window.localStorage.getItem("token"); //  
  const [currentTheme, setCurrentTheme] = useState(theme.OrangeTheme); //   
  const [selectedDate, setSelectedDate] = useState("");

  // jwt  userid  
  const getUserIdFromToken = () => {
    const payload = token.split(".")[1];
    const base642 = payload.replace(/-/g, "+").replace(/_/g, "/");
    const decodedPayload = atob(base642);
    const decodedJSON = JSON.parse(decodedPayload);

    console.log(decodedJSON);
    return decodedJSON.id.toString();
  };

  const fetchUserData = async userToken => {
    const userId = getUserIdFromToken(userToken); //  ID 
    try {
      const response = await axios.get(`/api/v1/userInfo/${userId}`, {
        headers: {
          Authorization: `Bearer ${userToken}`,
        },
      });
      //       
      const userThemeName = response.data.userColor; //   

      //    
      if (theme[userThemeName]) {
        setCurrentTheme(theme[userThemeName]);
      }
    } catch (error) {
      console.error("Error fetching user data:", error);
    }
  };
  const { teamId } = useParams();

  const fetchTeamDetails = async () => {
    try {
      const response = await axios.get(`/api/v1/getTeamInfo/${teamId}`, {
        headers: { Authorization: `Bearer ${token}` },
      });
      if (response.status === 200) {
        setTeamDetails(response.data); //  response.data teamId  .
        console.log(response.data); // response.data     .
      }
    } catch (error) {
      console.error("Failed to fetch team details:", error);
    }
  };

  useEffect(() => {
    if (teamId) {
      // teamId URL  .
      fetchTeamDetails(teamId); // fetchTeamDetails  teamId .
      fetchUserData();
    }
  }, [teamId, token]); // team   useEffect .
  const setDate = date => {
    setSelectedDate(date);
  };
  return (
    <ThemeProvider theme={currentTheme}>
      <NavBar setDate={setDate} />

      <MainDiv>
        <DateHeader>{teamDetails}</DateHeader>
        {/*       ,    . */}
        <CoopTodo
          teamId={teamId}
          onTodoChange={fetchTeamDetails}
          selectedDate={selectedDate}
        />
        <Memo teamId={teamId} />
      </MainDiv>
    </ThemeProvider>
    // Use 'team' prop to display team details
  );
}
export default CoopDetail;
