import React, { useState, useEffect } from "react";
import styled, { ThemeProvider } from "styled-components";
import { Navigate, useNavigate, Link } from "react-router-dom";
import Calendar from "react-calendar";
import axios from "axios"; // axios import
import USER from "../asset/userimage.png"; //   
import theme from "../styles/theme";
import NOTI from "../asset/KakaoTalk_20240126_160049425.png";
import "react-calendar/dist/Calendar.css"; //   .
import Todo from "../pages/Todo"; // Todo  import
import Diary from "../pages/Diary"; // Diary  import
import Coop from "../pages/Coop"; // Coop  import
import Setting from "../pages/Setting"; // Setting  import
import Stat from "../pages/Stat"; // Stat  import
import CoopDetail from "../pages/CoopDetail";
import NavBar from "../components/Navigation";

const PageLayout = styled.div`
  display: flex;
  flex-direction: column;
  height: 100vh;
  overflow-y: auto;
  &::-webkit-scrollbar {
    width: 4px;
  }
  &::-webkit-scrollbar-thumb {
    border-radius: 2px;
    background: #ccc;
  }
`;

const Header = styled.header`
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: ${props => props.theme.color1 || theme.OrangeTheme.color1};
  padding: 0 20px;
  height: 80px;
  position: fixed;
  width: 100%;
  z-index: 1;
`;

const Logo = styled.img`
  height: 50px; //    
  width: auto;
`;

const Navigation = styled.nav`
  display: flex;
  align-items: center;
  height: 80px;
  width: 100%;
  justify-content: right;
  padding-right: 40px;

  & > ul {
    display: flex;
    list-style: none;
    padding: 0;
    color: white;
  }

  & > ul > li {
    margin-left: 30px;
  }
`;

const Content = styled.main`
  display: flex;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
`;

const LeftSidebar = styled.aside`
  position: fixed; //   
  left: 0; //  
  top: 80px; //   
  width: 300px; //   
  height: calc(100vh - 80px); //      
  background-color: #f9f9f9; //  
  /* border-left: 1px solid
    ${props => props.theme.color1 || theme.OrangeTheme.color1}; */
  overflow-y: auto; //    
  padding: 20px;
  box-sizing: border-box; //   

  //   
  @media (max-width: 1050px) {
    //   1200px   
    display: none;
  }
`;

const MainContent = styled.section`
  flex-grow: 1;
  padding: 20px;
  margin-top: 80px;
  // margin-left: 300px; //  LeftSidebar   .

  //   
  @media (max-width: 1050px) {
    // LeftSidebar   
    margin-left: 0; // LeftSidebar     
  }
  &::-webkit-scrollbar {
    display: none;
  }
`;

const RightSidebar = styled.aside`
  position: fixed; //   
  right: 0; //  
  top: 80px; //   
  width: 300px; //   
  height: calc(100vh - 80px); //      
  background-color: #f9f9f9; //  
  /* border-left: 1px solid
    ${props => props.theme.color1 || theme.OrangeTheme.color1}; */
  overflow-y: auto; //    
  padding: 20px;
  box-sizing: border-box; //   
`;

const GreetingSection = styled.div`
  display: flex;
  align-items: center;
  background-color: white;
  border: 2px solid ${props => props.theme.color1 || theme.OrangeTheme.color1};
  border-radius: 20px;
  padding: 20px;
  margin: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
`;
const UserProfileImage = styled.img`
  margin-left: 10px;
  border-radius: 50%;
  height: 40px;
  width: 40px;
  object-fit: cover;
  box-shadow: 0px 0px 10px 5px
    ${props => props.theme.color1 || theme.OrangeTheme.color1}; //  
  border: 3px solid ${props => props.theme.color1 || theme.OrangeTheme.color1};
`;

const GreetingText = styled.div`
  margin-left: 20px;
  display: flex; //   
  flex-direction: column; //     
  align-items: flex-start; //   
  justify-content: center; //   
  color: ${props => props.theme.color1 || theme.OrangeTheme.color1};
  font-size: 15px; //   
`;

const StyledCalendar = styled(Calendar)`
  width: 100%;
  max-width: 300px; //    
  background-color: white;
  border: 1px solid ${props => props.theme.color1 || theme.OrangeTheme.color1};
  border-radius: 10px; //   
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  /* font-family: Arial, sans-serif; //   */

  .react-calendar__navigation button {
    border-radius: 50%;
    color: ${props => props.theme.color1 || theme.OrangeTheme.color1};
  }

  .react-calendar__tile:enabled:hover,
  .react-calendar__tile:enabled:focus {
    border-radius: 50%;
    background-color: ${props =>
      props.theme.color1 || theme.OrangeTheme.color1};
  }

  /*    */
  .react-calendar__month-view__weekdays__weekday--weekend abbr[title=""] {
    color: #c70000;
  }

  .react-calendar__tile--now {
    //    
    border-radius: 50%;
    background: ${props => props.theme.color2 || theme.OrangeTheme.color2};
    color: var(--festie-gray-800, #ffffff);
  }

  .react-calendar__month-view__weekdays abbr {
    //   
    text-decoration: none;
  }

  .react-calendar__month-view__days__day--weekend {
    color: inherit; //     
  }

  .react-calendar__tile--active {
    background-color: ${props =>
      props.theme.color2 || theme.OrangeTheme.color2};
    color: white;
    border-radius: 50%;
  }
`;

function Main() {
  const [currentTheme, setCurrentTheme] = useState(theme.OrangeTheme);
  const formatDay = (locale, date) => <span>{date.getDate()}</span>;
  const [userNickname, setUserNickname] = useState("");
  const [selectedDate, setSelectedDate] = useState(new Date()); //   
  const [selectedComponent, setSelectedComponent] = useState("Todo");
  const [diaryId, setDiaryId] = useState(null); //   ID 
  const token = window.localStorage.getItem("token"); //  
  const [selectedTeam, setSelectedTeam] = useState(null);
  // Base64    
  const [base64Image, setBase64Image] = useState("");

  const handleMenuClick = component => {
    setSelectedComponent(component);
  };

  const handleSelectTeam = team => {
    setSelectedTeam(team);
    setSelectedComponent("CoopDetail");
  };

  // jwt  userid  
  const getUserIdFromToken = () => {
    const payload = token.split(".")[1];
    const base642 = payload.replace(/-/g, "+").replace(/_/g, "/");
    const decodedPayload = atob(base642);
    const decodedJSON = JSON.parse(decodedPayload);

    console.log(decodedJSON);
    return decodedJSON.id.toString();
  };

  useEffect(() => {
    async function fetchUserData() {
      const userId = getUserIdFromToken(); //  ID 
      try {
        if (!token) return; //   

        //  ID  (    )
        // const userId = getUserIdFromToken();

        const response = await axios.get(`/api/v1/userInfo/${userId}`, {
          //     API 
          headers: {
            Authorization: `Bearer ${token}`,
          },
        });
        const userThemeName = response.data.userColor; //    
        const userProfileImage = response.data.userProfile; //   
        setUserNickname(response.data.userNickname); //   

        if (userThemeName && theme[userThemeName]) {
          setCurrentTheme(theme[userThemeName]); //    
        }

        //     
        setBase64Image(userProfileImage);
      } catch (error) {
        console.error("Error fetching user theme:", error);
      }
    }

    fetchUserData();
  }, [token]);

  const handleDateChange = value => {
    setSelectedDate(value); // Calendar     
  };

  // selectedComponent  'DiaryDetail'   Diary  .
  const renderDiaryDetail = id => {
    setDiaryId(id);
    setSelectedComponent("DiaryDetail");
  };

  const renderComponent = () => {
    switch (selectedComponent) {
      case "Todo":
        return <Todo selectedDate={selectedDate} />;
      case "Diary":
        return <Diary onDiarySelect={setDiaryId} />;
      case "Coop":
        return (
          <Coop
            onSelectTeam={team => {
              setSelectedTeam(team); //   
              setSelectedComponent("CoopDetail");
            }}
          />
        );
      case "CoopDetail":
        return <CoopDetail team={selectedTeam} selectedDate={selectedDate} />;
      case "Stat":
        return <Stat />;
      case "Setting":
        return <Setting />;
      default:
        return <Todo />;
    }
  };

  return (
    <ThemeProvider theme={currentTheme}>
      <PageLayout>
        <Header>
          {/* <NavBar /> */}
          <li
            onClick={() => handleMenuClick("Coop")}
            style={{ cursor: "pointer" }}
          >
            
          </li>
        </Header>
        <Content>
          <LeftSidebar>
            <GreetingSection>
              <UserProfileImage src={base64Image || USER} alt="User Profile" />
              <GreetingText>
                <div>{userNickname} ,</div> {/*     */}
                <div style={{ color: "black" }}>!</div>
              </GreetingText>
            </GreetingSection>
            {/*    */}
          </LeftSidebar>
        </Content>
        <MainContent>
          {renderComponent()} {/*    */}{" "}
        </MainContent>
        <RightSidebar>
          <StyledCalendar
            onChange={handleDateChange}
            value={selectedDate}
            formatDay={formatDay}
          />
        </RightSidebar>
      </PageLayout>
    </ThemeProvider>
  );
}

export default Main;
