/* eslint-disable no-unused-vars */
/* eslint-disable prettier/prettier */
import React, { useState, useEffect } from "react";
import styled, { ThemeProvider } from "styled-components";
import { useLocation, useNavigate, Link } from "react-router-dom";
import axios from "axios"; // axios import 
import theme from "../styles/theme";
import NOTI from "../asset/KakaoTalk_20240105_025742662.png";
import STAR from "../asset/star.png";

const MainDiv = styled.div`
  // 
  display: flex;
  flex-basis: auto;
  flex-direction: column; //  
  align-items: center; //  
  justify-content: center; //  
  width: 100%;
  background-color: #333;
  height: 100vh;
`;

const MainTextBox = styled.div`
  //  
  letter-spacing: 1px;
  color: ${props => props.theme.color1}; //   color1 
  text-align: center;
  font-size: 40px;
  width: 100%;
  height: 30px;
  font-weight: normal;
`;

const WelBtn = styled.button`
  //  
  border: none;
  width: 350px;
  height: 40px;
  border-radius: 40px; //  
  background-color: ${props => props.theme.color1 || theme.OrangeTheme.color1};
  color: #ffffff;
  font-size: 12px; // 
  font-weight: bold;
  letter-spacing: 1px; //  
  margin-top: 80px;
  // transition: transform 80ms ease-in; //  
  text-align: center; //   
`;
const ImgBox = styled.div`
  //  div
  position: relative;
  width: 350px;
  height: 350px;
`;

const GestImgBox = styled.img`
  //   
  position: absolute;
  width: 280px;
  height: 280px;
  margin-top: 60px;
  margin-bottom: 60px;
`;

function Welcome() {
  const location = useLocation();
  const navigate = useNavigate();
  const token = window.localStorage.getItem("token"); //  
  const [currentTheme, setCurrentTheme] = useState(theme.OrangeTheme); //    

  // Base64    
  const [base64Image, setBase64Image] = useState("");

  // jwt  userid  
  const getUserIdFromToken = () => {
    const payload = token.split(".")[1];
    const base642 = payload.replace(/-/g, "+").replace(/_/g, "/");
    const decodedPayload = atob(base642);
    const decodedJSON = JSON.parse(decodedPayload);

    console.log(decodedJSON);
    return decodedJSON.id.toString();
  };

  useEffect(() => {
    async function fetchUserData() {
      const userId = getUserIdFromToken(); //  ID 
      try {
        const response = await axios.get(`/api/v1/userInfo/${userId}`, {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        });
        //       
        const userThemeName = response.data.userColor; //   
        const userProfileImage = response.data.userProfile; //   

        //    
        if (theme[userThemeName]) {
          setCurrentTheme(theme[userThemeName]);
        }

        //     
        setBase64Image(userProfileImage);
      } catch (error) {
        console.error("Error fetching user data:", error);
      }
    }

    fetchUserData();
  }, [token]);

  //    
  const handleCompleteClick = () => {
    navigate("/main"); //   
  };

  return (
    <ThemeProvider theme={currentTheme}>
      <div>
        <MainDiv>
          <MainTextBox
            style={{ color: currentTheme.color1, marginBottom: "20px" }}
          >
              !
          </MainTextBox>
          <MainTextBox style={{ fontWeight: "700", color: "#ffffff" }}>
             !    
          </MainTextBox>
          <ImgBox>
            <GestImgBox
              src={base64Image || NOTI} //  NOTI  
              style={{
                top: "50%",
                left: "50%",
                transform: "translate(-50%, -50%)",
                width: "200px",
                height: "200px",
                borderRadius: "50%",
                boxShadow: "color: userColor",
              }}
            />
            <GestImgBox
              src={STAR}
              style={{
                top: "50%",
                left: "45%",
                transform: "translate(-50%, -50%)",
                width: "100%",
                height: "100%",
              }}
            />
          </ImgBox>
          <WelBtn
            onClick={handleCompleteClick}
            style={{ backgroundColor: currentTheme.color1 }}
          >
            
          </WelBtn>
        </MainDiv>
      </div>
    </ThemeProvider>
  );
}
export default Welcome;
